import React, { createRef, useEffect, useRef, useState } from 'react';
import { Button, Form, FormGroup, FormLabel, Modal, ModalBody, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import {loadReCaptcha, ReCaptcha} from 'react-recaptcha-v3';
import {reCAPTCHA_SITE_KEY} from '../../constants';
import axios from 'axios';
import './styles.css';

export const NavbarComponent = (props: any) => {
    const [control, setControl] = useState({contactModal: false, captchaok: false, error : false, success: false});
    let recaptcha: null | ReCaptcha;
    

    const ContactModal = () => {
        useEffect(() => {
            loadReCaptcha(reCAPTCHA_SITE_KEY);
            setControl({...control, error: false});
        }, [])
        const updateToken = () => {
            if(!recaptcha) return;
            recaptcha.execute();
            return;
        }
        const handleCaptcha = async (token : string | null) => {
            
            try {
                console.log(token);
                if(!token) return;
                
                
                //@ts-ignore
                const a = document.getElementById('contact')?.value;
                //@ts-ignore
                const b = document.getElementById('subject')?.value;
                //@ts-ignore
                const c = document.getElementById('messageBody')?.value;

                if(!a || !b || !c) {
                    setControl({...control, error: true});
                    return;
                }
                else{
                    setControl({...control, error: false});
                }

                const res = await axios.post('https://us-central1-web-folio-d329b.cloudfunctions.net/verify_reCAPTCHA',{token: token, contact: a, subject: b, message: c});

                if(res.status === 200){
                    setControl({...control, success: true});
                }
                else{
                    console.log(res.data);
                    setControl({...control, error: true});
                }
                return;
            } catch (err) {
                console.log(err);
                
            }
        }

        const renderMessage = () => {
            if(control.error){
                return(
                    <p color='danger'>An error has occured. Did you fill all the fields?</p>
                );
            }
            else if(control.success){
                return(
                    <p color='success'>Sent!</p>
                );
            }
            else{
                return null;
            }
        }

        return(
            <Modal show={control.contactModal} onHide={() => {setControl({...control, contactModal: false})}}>
                <ModalBody>
                    <Form>
                        <FormGroup>
                            <FormLabel>Email Address / Phone Number<span color='red'>*</span></FormLabel>
                            <Form.Control id='contact' type='email' placeholder='Where to get back'/>
                        </FormGroup>
                        <FormGroup>
                            <FormLabel>Subject</FormLabel>
                            <Form.Control id='subject' placeholder='What is this about?'/>
                        </FormGroup>
                        <FormGroup>
                            <FormLabel>Message: </FormLabel>
                            <Form.Control id='messageBody' as="textarea" rows={8}/>
                        </FormGroup> 
                        <FormGroup>
                            <ReCaptcha
                                sitekey= {reCAPTCHA_SITE_KEY}
                                action='Message'
                                verifyCallback={(res) => handleCaptcha(res)}
                                ref={ref => recaptcha = ref}
                            />
                        </FormGroup>
                        {/* <FormGroup>
                            <FormLabel> {renderMessage()} </FormLabel>
                        </FormGroup> */}
                        <FormGroup>
                            <Button onClick={() => updateToken()} style={{backgroundColor: '#4E4E50', borderColor:'#4E4E50' }}>Send</Button>

                        </FormGroup>
                    </Form>
                </ModalBody>
            </Modal>
        )
    }

    return(
        <div>
            <Navbar className='justify-content-between' sticky="top" style={{backgroundColor: "#1A1A1D"}}>
                <Navbar.Brand><div style={{color:"#C3073F"}}>Amartya Mishra</div></Navbar.Brand>
                <NavDropdown alignRight id='menu-btn' title="Menu">
                    <NavDropdown.Item onClick={() => {window.open('https://1drv.ms/b/s!Akkr1QYilOQMicl_cFCz16x1wC_Gdg?e=Vd4cOc')}}>View Resume</NavDropdown.Item>
                    <NavDropdown.Item onClick={() => setControl({...control, contactModal: true})}>Say Something</NavDropdown.Item>
                    <NavDropdown.Divider style={{backgroundColor: '#00000030'}} />
                    <p className='ver-num px-4'>v0.1.4</p>
                </NavDropdown>
            </Navbar>
            {ContactModal()}
        </div>
    )
}